<script>
import { registroProponentes } from '@/services/api'

export default {
  name: 'regPerNatural',
  props: ['codigo', 'email', 'dataPrefijos', 'dataPaises', 'dataCiudades', 'dataRegistro'],
  data: () => ({
    valid: false,
    pnidentificacion: '',
    primernombre: '',
    segundonombre: '',
    primerapellido: '',
    segundoapellido: '',
    prefijopntelefono: '',
    pntelefono: '',
    prefijopncelular: '',
    celular: '',
    fechanacimiento: '',
    pnpaiscod: '',
    pnciudadcodcol: '',
    pnnomciudadcol: '',
    pnciudadint: '',
    direccioncorres: '',
    paiscorrescod: '',
    paiscorresnom: '',
    ciudadcorrescodcol: '',
    ciudadcorresnomcol: '',
    ciudadcorresint: '',
    estado: '',
    estadotxt: '',
    fechacreacion: '',
    pnrut: '',
    rules: {
      required: value => !!value || 'Campo requerido.',
      valcero: value => value !== 0 || 'El Valor debe ser diferente a cero (0).'
    },
    emailRules: [
      v => !!v || 'Email es requerido.',
      v => /.+@.+\..+/.test(v) || 'Debe ser un email valido.'
    ]
  }),
  methods: {
    async getData () {
      this.pnidentificacion = this.dataRegistro.pnidentificacion
      this.primernombre = this.dataRegistro.primernombre
      this.segundonombre = this.dataRegistro.segundonombre
      this.primerapellido = this.dataRegistro.primerapellido
      this.segundoapellido = this.dataRegistro.segundoapellido
      this.prefijopntelefono = parseInt(this.dataRegistro.prefijopntelefono)
      this.pntelefono = this.dataRegistro.pntelefono
      this.prefijopncelular = parseInt(this.dataRegistro.prefijopncelular)
      this.celular = this.dataRegistro.celular
      this.fechanacimiento = this.dataRegistro.fechanacimiento
      this.pnpaiscod = this.dataRegistro.pnpaiscod
      this.pnciudadcodcol = this.dataRegistro.pnciudadcodcol
      this.pnciudadint = this.dataRegistro.pnciudadint
      this.direccioncorres = this.dataRegistro.direccioncorres
      this.paiscorrescod = this.dataRegistro.paiscorrescod
      this.ciudadcorrescodcol = this.dataRegistro.ciudadcorrescodcol
      this.ciudadcorresint = this.dataRegistro.ciudadcorresint
      this.pnrut = this.dataRegistro.pnrut
    },
    async salvarRegistro () {
      try {
        let codciudadcorrespondeciacol = ''
        let codciudadcol = ''
        if (this.ciudadcorrescodcol !== undefined) {
          codciudadcorrespondeciacol = this.ciudadcorrescodcol
        }
        if (this.pnciudadcodcol !== undefined) {
          codciudadcol = this.pnciudadcodcol
        }
        const datasave = { codigo: this.codigo, email: this.email, naturalezajuridica: 'N', pnidentificacion: this.pnidentificacion, primernombre: this.primernombre, segundonombre: this.segundonombre, primerapellido: this.primerapellido, segundoapellido: this.segundoapellido, prefijopntelefono: this.prefijopntelefono, pntelefono: this.pntelefono, prefijopncelular: this.prefijopncelular, celular: this.celular, fechanacimiento: this.fechanacimiento, pnpaiscod: this.pnpaiscod, pnciudadcodcol: codciudadcol, pnciudadint: this.pnciudadint, direccioncorres: this.direccioncorres, paiscorrescod: this.paiscorrescod, ciudadcorrescodcol: codciudadcorrespondeciacol, ciudadcorresint: this.ciudadcorresint, pnrut: this.pnrut }
        const { data } = await registroProponentes.saveRegistroNatural({
          data: datasave
        })
        if (data.respuesta) {
          this.$toasted.success('Se actualizarón los datos con éxito', { icon: 'done', duration: 5000 })
          this.$store.commit('updateRegistro', true)
        } else {
          // alert('Se presentó un error al tratar de guardar la información')
          this.$toasted.error('Se presentó un error al tratar de guardar la información', { icon: 'error', duration: 5000 })
        }
      } catch (error) {
      } finally {
      }
    }
  },
  watch: {
    dataRegistro: function (val) {
      if (val) {
        this.getData()
      }
    }
  },
  mounted () {
    this.getData()
  }
}
</script>
